import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { HikeDetails } from '../hike-details/hike-details';
import { Hike } from '../../model/hike';
import { OpenDataService } from '../../services/opendata-service';

@Component({
  selector: 'page-list',
  templateUrl: 'hikes-list.html'
})
export class HikesList {

  public hikes : Hike[];

  constructor(public navCtrl: NavController, public navParams: NavParams, dataService : OpenDataService) {
    dataService.fetch().then(parsedData => {
      this.hikes = parsedData;
    }).catch(err => {
      window.alert(`ERROR FETCHING DATA : ${err}`);
    });
  }

  itemTapped(evt, hike : Hike) {
    this.navCtrl.push(HikeDetails, {
      item: hike
    });
  }
}



